import { Component, forwardRef } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor, FormControl } from '@angular/forms';

import { AbstractControlValueAccessor } from '../abstract-control-value-accesor';

export const UPLOAD_INPUT_CONTROL_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => TdDynamicFileInputComponent),
  multi: true,
};

@Component({
  providers: [ UPLOAD_INPUT_CONTROL_VALUE_ACCESSOR ],
  selector: 'td-dynamic-file-input',
  styles: [`.td-dynamic-file-input-wrapper{
  -webkit-box-orient:horizontal;
  -webkit-box-direction:normal;
      -ms-flex-direction:row;
          flex-direction:row;
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  -webkit-box-sizing:border-box;
          box-sizing:border-box; }
  .td-dynamic-file-input-wrapper .td-dynamic-file-input-field{
    -webkit-box-flex:1;
        -ms-flex:1;
            flex:1;
    -webkit-box-sizing:border-box;
            box-sizing:border-box; }

.td-file-input{
  margin-left:10px; }
`],
  template: `<div class="td-dynamic-file-input-wrapper">
  <mat-form-field tdFileDrop
                  class="td-dynamic-file-input-field"
                  floatPlaceholder="never"
                  (fileDrop)="value = $event"
                  (click)="fileInput.inputElement.click()"
                  (keyup.enter)="fileInput.inputElement.click()"
                  (keyup.delete)="fileInput.clear()"
                  (keyup.backspace)="fileInput.clear()">
    <input matInput
          [value]="value?.name"
          [placeholder]="label"
          readonly/>
  </mat-form-field>
  <button mat-icon-button *ngIf="value" (click)="fileInput.clear()" (keyup.enter)="fileInput.clear()">
    <mat-icon>cancel</mat-icon>
  </button>
  <td-file-input class="td-file-input" #fileInput [(ngModel)]="value">
    <mat-icon>folder</mat-icon>
    <span>{{ label }}</span>
  </td-file-input>
</div>`,
})
export class TdDynamicFileInputComponent extends AbstractControlValueAccessor implements ControlValueAccessor {

  control: FormControl;

  required: boolean = undefined;

  label: string = '';

}
