import { Component, forwardRef } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor, FormControl } from '@angular/forms';

import { AbstractControlValueAccessor } from '../abstract-control-value-accesor';

export const SLIDER_INPUT_CONTROL_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => TdDynamicSliderComponent),
  multi: true,
};

@Component({
  providers: [ SLIDER_INPUT_CONTROL_VALUE_ACCESSOR ],
  selector: 'td-dynamic-slider',
  styles: [`.td-dynamic-slider-field{
  position:relative;
  margin-top:8px;
  -webkit-box-orient:horizontal;
  -webkit-box-direction:normal;
      -ms-flex-direction:row;
          flex-direction:row;
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  -webkit-box-sizing:border-box;
          box-sizing:border-box; }
  .td-dynamic-slider-field .td-dynamic-slider{
    -webkit-box-flex:1;
        -ms-flex:1;
            flex:1; }
`],
  template: `<div class="td-dynamic-slider-wrapper">
  <div class="mat-form-field-placeholder-wrapper mat-form-field-can-float mat-form-field-should-float"
       [class.mat-focused]="slider._isActive">
    <label class="mat-form-field-placeholder mat-float mat-form-field-float td-slider-label"> {{label}} <span *ngIf="required" class="mat-placeholder-required">*</span></label>
  </div>
  <div class="td-dynamic-slider-field">
    <mat-slider #slider
               class="td-dynamic-slider"
               [(ngModel)]="value"
                [min]="min"
                [max]="max"
                thumbLabel
                tickInterval="auto"
                [required]="required">
    </mat-slider>
  </div>  
</div>`,
})
export class TdDynamicSliderComponent extends AbstractControlValueAccessor implements ControlValueAccessor {

  control: FormControl;

  label: string = '';

  required: boolean = undefined;

  min: number = undefined;

  max: number = undefined;

}
