import { Component, forwardRef } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor, FormControl } from '@angular/forms';

import { AbstractControlValueAccessor } from '../abstract-control-value-accesor';

export const SELECT_TOGGLE_INPUT_CONTROL_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => TdDynamicSelectComponent),
  multi: true,
};

@Component({
  providers: [ SELECT_TOGGLE_INPUT_CONTROL_VALUE_ACCESSOR ],
  selector: 'td-dynamic-select',
  styles: [`.td-dynamic-select-wrapper{
  -webkit-box-orient:horizontal;
  -webkit-box-direction:normal;
      -ms-flex-direction:row;
          flex-direction:row;
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  -webkit-box-sizing:border-box;
          box-sizing:border-box; }
  .td-dynamic-select-wrapper .td-dynamic-select-field{
    -webkit-box-flex:1;
        -ms-flex:1;
            flex:1;
    -webkit-box-sizing:border-box;
            box-sizing:border-box; }
`],
  template: `<div class="td-dynamic-select-wrapper">
  <mat-form-field class="td-dynamic-select-field">
    <mat-select [(ngModel)]="value"
              [placeholder]="label"
              [required]="required">
      <mat-option *ngFor="let selection of selections" [value]="selection.value || selection">{{selection.label || selection}}</mat-option>
    </mat-select>
  </mat-form-field>
</div>
`,
})
export class TdDynamicSelectComponent extends AbstractControlValueAccessor implements ControlValueAccessor {

  control: FormControl;

  label: string = '';

  required: boolean = undefined;

  selections: any[] = undefined;

}
